.contacts {
  // position: relative;
  // outline: 1px solid red;
  background-color: #84a178;
  padding-bottom: 60px;

  // height: 1095px;
  @media screen and (min-width: 768px) {
    padding-bottom: 40px;
    // height: 360px;
  }

  @media screen and (min-width: 1200px) {
    padding-bottom: 120px;
    // height: 636px;
  }
}

.contacts__container {
  padding: 0 20px;
  margin: 0 auto;

  @media screen and (min-width: 480px) {
    width: 480px;
  }

  @media screen and (min-width: 768px) {
    width: 768px;
    padding: 0 32px;
  }

  @media screen and (min-width: 1200px) {
    width: 1200px;
    padding: 0 16px;
  }
}

.title-hiden {
  position: absolute;
  white-space: nowrap;
  width: 1px;
  height: 1px;
  overflow: hidden;
  border: 0;
  padding: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(50%);
  margin: -1px;
}

// контейнер карток
.contacts-wrapper {
  list-style: none;
  // transform: translateY(-352px);
  // position: absolute;

  @media screen and (min-width: 768px) {
    // transform: translateY(-300px);
    display: flex;
    gap: 16px;
  }

  @media screen and (min-width: 1200px) {
    // transform: translateY(-128px);
    gap: 32px;
  }
}

.contacts-wrapper__item {
  padding-top: 40px;
  padding-bottom: 24px;
  padding-right: 32px;
  padding-left: 32px;

  color: #131313;
  background: #ffffff;
  box-shadow: 0px 8px 30px rgba(212, 20, 67, 0.1);
  border-radius: 24px;
  font-family: 'Montserrat';
  font-weight: 500;

  @media screen and (min-width: 480px) {
    // width: 450px;
    width: 440px;
  }

  @media screen and (min-width: 768px) {
    width: 224px;
    padding-right: 24px;
    padding-left: 24px;
  }

  @media screen and (min-width: 1200px) {
    width: 368px;
    padding-right: 40px;
    padding-left: 40px;
  }

  &:not(:last-child) {
    @media screen and (max-width: 767px) {
      margin-bottom: 16px;
    }
  }
}

.wrapper-text {
  display: inline-flex;
  justify-content: center;
  align-items: center;

  padding: 4px 22px;
  margin-bottom: 20px;
  background: #f8d24e;
  border-radius: 4px;

  @media screen and (min-width: 768px) {
    padding: 4px 13px;
    margin-bottom: 32px;
  }

  @media screen and (min-width: 1200px) {
    padding: 4px 14px;
  }
}

.wrapper-name {
  display: block;
  font-size: 12px;
  line-height: 1.2;
  letter-spacing: 0.04em;

  @media screen and (min-width: 768px) {
    font-size: 16px;
    line-height: 1.4;
  }
}

.contacts__address {
  height: 70px;
  font-style: normal;
  margin-bottom: 24px;
  line-height: 1.86;
  letter-spacing: 0.04em;
  font-size: 12px;

  @media screen and (min-width: 768px) {
    height: 109px;
    margin-bottom: 32px;
    font-size: 14px;
  }

  @media screen and (min-width: 1200px) {
    height: 88px;
    font-size: 16px;
  }
}

.contacts__address-city {
  padding: 0;
  padding-bottom: 8px;
}

.address-link {
  display: block;
  color: #d6936d;
  line-height: 1.83;
  transition: text-decoration 250ms cubic-bezier(0.4, 0, 0.2, 1);

  &:hover,
  &:focus {
    text-decoration: underline;
  }
}

.schedule-list {
  padding-top: 24px;
  padding-bottom: 24px;
  margin-bottom: 24px;
  font-weight: 500;
  font-size: 12px;
  line-height: 1.86;
  color: #131313;
  border-top: 1px solid #e1e1e1;
  border-bottom: 1px solid #e1e1e1;

  @media screen and (min-width: 768px) {
    font-size: 14px;
    padding-top: 32px;
    padding-bottom: 32px;
    margin-bottom: 32px;
  }

  @media screen and (min-width: 1200px) {
    font-size: 16px;
  }
}

// .schedule-list__item{
//    outline: 1px solid tomato;
// }

.schedule-list__item:not(:last-child) {
  margin-bottom: 8px;
}

// .schedule-list__day{}

.schedule-list__time {
  color: #ababab;
}

.contacts-list {
  font-weight: 500;
  font-size: 12px;
  line-height: 1.6;
  color: #131313;

  @media screen and (min-width: 768px) {
    font-size: 14px;
    line-height: 1.5;
  }

  @media screen and (min-width: 1200px) {
    font-size: 16px;
  }
}

.contacts-list__item {
  color: inherit;
}

.contacts-list__item:not(:last-child) {
  margin-bottom: 8px;
}

// .contacts-list__link{
// }

.button-list {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  gap: 16px;
  margin-top: 20px;
  // margin-bottom: 60px;

  @media screen and (min-width: 768px) {
    flex-direction: row;
    margin-top: 40px;
    // margin-bottom: 40px;
  }

  @media screen and (min-width: 1200px) {
    margin-top: 60px;
    // margin-bottom: 120px;
  }
}

.button-list__item {
  display: block;
}

.contacts__button {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  font-family: 'Montserrat';
  font-weight: 600;
  font-size: 14px;
  line-height: 1.285;

  width: 196px;
  height: 42px;
  // cursor: pointer;

  color: #000000;
  border-radius: 10px;
  border: 2px solid #f8d24e;
}

.contacts__button-locations {
  background-color: #f8d24e;
  transition: background-color 250ms cubic-bezier(0.4, 0, 0.2, 1),
    border 250ms cubic-bezier(0.4, 0, 0.2, 1);

  &:hover {
    background-color: #0a8817;
    border: 2px solid #0a8817;
  }

  &:focus {
    background-color: #0a8817;
    border: 2px solid #0a8817;

    & .contacts__button-text {
      color: #ffffff;
    }

    & .icon-menu--order {
      fill: #ffffff;
    }
  }
}

.contacts__button-franchise {
  background-color: transparent;
  transition: background-color 250ms cubic-bezier(0.4, 0, 0.2, 1),
    border 250ms cubic-bezier(0.4, 0, 0.2, 1);

  &:hover {
    border: 2px solid #0a8817;
  }

  &:focus {
    border: 2px solid #0a8817;

    & .contacts__button-text {
      color: #ffffff;
    }

    & .icon-menu--order {
      fill: #ffffff;
    }
  }
}
